import NetWork from './NetWork'

export const postSignUp = (data) => NetWork.post('/api/user/register', data)
export const postCode = (data) => NetWork.post('/api/user/code', data)
export const postSignIn = (data) => NetWork.post('/api/user/login', data)
export const getSignOut = (data) => NetWork.post('/api/user/logout', data)
export const postRePass = (data) => NetWork.post('/api/user/fpassword', data)
export const getInfo = (data) => NetWork.get('/api/index/page', data)
export const getClass = (data) => NetWork.get('/api/index/class2', data)
export const getInst = (data) => NetWork.get('/api/index/other', data)
